___CSS_LOADER_EXPORT___.push([module.id, `.boxAnAsideProject{
    display: flex;
    justify-content: space-between;
    padding: 5px;
    border: 2px solid var(--main-color);
    border-radius: 5px;
    cursor: pointer;
    width:  150px;
    text-overflow:clip;
    color: var(--main-color);
}

.btnRemoveProject{
    background-color: var(--main-color);
    color: white;
    font-weight: 100;
    font-size: 0.8rem;
    border-radius: 2px;
    border: none;
    padding: 3px;
    cursor: pointer;
    height: 30px;
    width: 30px;
    font-size: 1.2rem;
}

.boxToTheBoxLol{
    display: flex;
    gap: 5px;
    justify-content: space-between;
    align-items: center;
}


/*  aside  */

#aside{
    width: 200px;
    height: auto;
    border-right: 1px solid var(--main-color);
    grid-row: 2 / 3;
    display: flex;
    padding-top: 20px;
    padding-left: 10px;
    padding-right: 10px;
    flex-direction: column;
    justify-content: space-between;
    padding-bottom: 20px;
}

#asideProjectList{
    width: 100%;
    height: 60px;
    display: flex;
    flex-direction: column;
    gap: 8px;
}

#divCreateProject{
    align-self: center;
    padding: 5px;
}


.btnCreateProject{
    background-color: var(--main-color);
    color: white;
    font-weight: 700;
    padding: 5px 10px;
    border-radius: 5px;
    border: 1px solid white;
}
