:root{
    --main-color: #3d0c11
}

*{
    padding: 0px;
    margin: 0px;
    box-sizing: border-box;

}

body{
    display: grid;
    grid-template-columns: 200px 4fr;
    grid-template-rows: 140px 1fr; 
    height: 100vh;
    width: 100vw;
}

header{
    align-items: center;
    width: 100%;
    height: 140px;
    background-color: var(--main-color) ;
    grid-column:1 / 3; 
    display: grid;
    grid-template-columns: 200px 2fr;
    justify-items: center;
}


.boxAvatar{
    width: 100px;
    height: 100px;
    overflow: hidden;
    border: none;
    border-radius: 100%;
    display: flex;
    justify-content: center;
    border: 3px white solid;
}

#avatar{
    width: 200px;
    height: auto;
}


.headerTitle{
    color: white;
    font-size: 3.5rem;
    font-weight: 800;
    font-family: Helvetica, sans-serif;
}




