___CSS_LOADER_EXPORT___.push([module.id, `*{
    padding: 0px;
    margin: 0px;
}

#dialogNewTodo{
    margin: 0 auto;
    margin-top: 200px;
    border: 1px solid white;
    border-radius: 10px;
}

#dialogNewTodo::backdrop{
    opacity: 0.8;
    background-color: #3d0c11;
}

.formNewToDo{
    width: 250px;
    height: 300px;
    border-radius: 10px;
    border: 1px solid black;
    margin: 20px;
    box-shadow: 0px 0px 2px 1px #3d0c11 inset;
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.addNewToDo{
    font-size: 1.5rem;
    padding: 5px;
    background-color: #3d0c11;
    border: 1px solid #3D0C11;
    color: white;
    border-radius: 10px 10px 0px 0px;
    margin-bottom: 20px;
}

label{
    font-size: 0.8rem;
    margin-left: 10px;
    color: #3d0c11;
}

#inputDescriptionNewToDo{
    height: 60px;
}

.boxBtns{
    display: flex;
    align-self: flex-end;
}

.createToDo{
    margin: 10px;
    padding: 5px 10px;
    color: white;
    background-color: #3D0C11;
    font-size: 0.8rem;
    border: none;
    border-radius: 10px;
    cursor: pointer;
}

.cancelToDo{
    margin: 10px;
    padding: 5px 10px;
    font-size: 0.8rem;
    border: 1px solid black;
    background-color: white;
    border-radius: 10px;
    cursor: pointer;
}


select{
    width: 120px;
    margin-left: 10px;
}


.btnEditToDo{
    margin: 10px;
    padding: 5px 10px;
    color: white;
    background-color: #3D0C11;
    font-size: 0.8rem;
    border: none;
    border-radius: 10px;
    cursor: pointer;
}

